.certifications {
  margin: 0 auto;
}
.certifications h1{
  font-size: 40px;
  padding: 45px 0;
}

.certifications a {
  color: inherit;
  font-family: 'Cinzel', serif;
}

.certifications__info {
  background-color: #fff;
  color: #000;
  margin-top: 30px;
  border-radius: 10px;
  padding: 19px 5px;
}


.certifications__info h2{
  font-size: 20px;
  padding: 20px 0;

}

.certifications__info ul {
  list-style: inside;
}

.certifications__info li {
  color: #000;
  font-family: Cinzel;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-bottom: 8px;
}

@media screen and (min-width: 768px) {
  .certifications {
    width: 80%
  }
}