.work-experience{
  padding: 45px 20px;
  margin: 0 auto; 
}


.work-experience h1{
  font-size: 40px;
  margin-bottom: 50px;
}

.work-experience ul {
  list-style: inside;
  color: #FFF;
  font-family: Cinzel;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.work-experience h2 {
  font-size: 25px;
}

.work-experience p {
  margin-bottom: 15px;
}

.job__period {
  font-size: 12px;
}

@media screen and (min-width: 768px) {
  .work-experience {
    width: 80%
  }
}